body {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: rgb(21, 173, 173);
}

.hello {
  display: inline-block;
}

.body {
    width: 800px;
    margin: 0 auto;
    text-align: center;
}

.header {
    display: inline-block;
    width: 40%;
    padding: 5px;
    font-family: 'Courier New', Courier, monospace;
    font-size: 40px;
}

.tableContainer {
    text-align: center;
    background-color: rgb(250, 182, 56);
    padding: 20px;
    border: 3px solid plum;
    width: 700px;
}

table {
    display: inline-block;
    text-align: left;
    table-layout: fixed;
    width: 90%;
    border: 3px solid turquoise;
    background-color: aliceblue;
    padding: 20px;
    border: solid 2px black;
  }

  td {
    padding-left: 50px;
    padding-right: 50px;
    font-size: 18px;
    border: solid 2px turquoise;
  }

  th {
    padding-left: 50px;
    font-size: 20px;
    border: solid 2px turquoise;
  }

  thead tr{
    color: black;
    font-weight: bold;
  }

  footer {
    margin: 30px;
    padding: 20px;
    text-align: center;
    font-size: 20px;
}

.home {
  text-align: center;
}

.content {
  display: inline-block;
  background-color: rgb(82, 193, 221);
  padding: 20px;
  margin: 40px;
  border: 3px solid black;
  width: 640px;
  height: 400px;
  text-align: left;
}

.aboutContent {
  display: inline-block;
  background-color: rgb(82, 193, 221);
  padding: 20px;
  margin: 20px;
  border: 3px solid black;
  width: 800px;
  height: 600px;
  text-align: center;
}

h1 {
  padding: 10px;
  font-size: 40px;
  color: aliceblue;
  font-weight: bold;
}

h2 {
  font-weight: bold;
  color: aliceblue;
  text-align: center;
}

h3 {
  color: black;
}

.reactBanner {
  width : 950px;
  height: 160px;
  border: 3px solid rgb(15, 126, 170);
}

.codeCart {
  width: 400px; 
  height: 400px;
  margin: 10px;
  border: 2px solid rgb(179, 176, 12);
}